import React, { useState, useRef, useMemo, useCallback } from 'react';
import { TextureLoader } from 'three/src/loaders/TextureLoader';
import { useFrame, useThree, useUpdate } from 'react-three-fiber';

const positionArr = [
  [0, 0, 0],
  [0, 0, 200],
];

function AllScene() {
  return (
    <group>
      <MainScene position={positionArr[0]} />
    </group>
  );
}

function MainScene(props) {
  const { onClick, position } = props;
  return (
    <group position={position}>
      <SkyBox textureUrl="./bg-park.jpg" />
      <DirectionalSign size={[15, 15]} position={[0, 0, 80]} scene={1} onClick={onClick} />
    </group>
  );
}

function NorthScene(props) {
  const { onClick, position } = props;
  return (
    <group position={position}>
      <SkyBox textureUrl="./bg-house.jpg" />
      <DirectionalSign size={[30, 10]} position={[0, -50, -70]} scene={0} onClick={onClick} />
    </group>
  );
}

function SkyBox(props) {
  const texture = useMemo(() => new TextureLoader().load(props.textureUrl), [props.textureUrl]);
  const myRef = useUpdate((ref) => ref.scale(-1, 1, 1), []);
  return (
    <mesh>
      <sphereGeometry ref={myRef} attach="geometry" args={[100, 50, 50]} />
      <meshBasicMaterial attach="material" transparent>
        <primitive attach="map" object={texture} />
      </meshBasicMaterial>
    </mesh>
  );
}

function DirectionalSign(props) {
  const ref = useRef();
  const { camera } = useThree();
  const { onClick, scene } = props;
  const [color, changeColor] = useState('#ff3333');
  useFrame(() => ref.current.rotation.setFromRotationMatrix(camera.matrix));

  return (
    // <mesh ref={ref} onClick={(e) => onClick(scene)} onTap={(e) => onClick(scene)} onPointerOver={(e) => changeColor('#3333ff')} onPointerOut={(e) => changeColor('#ff3333')} position={props.position}>
    <mesh ref={ref} onTap={(e) => onClick(scene)} onPointerOver={(e) => changeColor('#3333ff')} onPointerOut={(e) => changeColor('#ff3333')} position={props.position}>
      <planeGeometry attach="geometry" args={props.size} />
      <meshBasicMaterial attach="material" color={color} />
    </mesh>
  );
}

export { AllScene };
