import React, { Fragment, useRef } from 'react';
import { Canvas, extend, useFrame, useThree } from 'react-three-fiber';
import { AxesHelper } from 'three/src/helpers/AxesHelper';
// import { PcControls } from '_controls.js';

import { OrbitControls } from 'three/examples/jsm/controls/OrbitControls';
import { Vector3 } from 'three/src/math/Vector3';
import * as THREE from 'three/build/three.module.js';
import { AllScene } from '_scenes.js';

extend({ AxesHelper });

extend({ OrbitControls });

function PcControls() {
  const { camera, gl } = useThree();
  const controls = useRef();
  useFrame(() => controls.current.update());
  return <orbitControls ref={controls} args={[camera, gl.domElement]} target={new THREE.Vector3(0, 0, 1)} />;
}

function App() {
  return (
    <Fragment>
      <Canvas camera={{ position: [0, 0, 0] }} xr="true" raycaster={{ filter: (items) => items.slice(0, 1) }}>
        <ambientLight />
        <PcControls />
        <AllScene />
        <axesHelper args={[300]} />
      </Canvas>
    </Fragment>
  );
}

export default App;
